<template>
  <div class="user-list-container">
    <div class="page-header">
      <div class="title-section">
        <h2 class="page-title">用户管理中心</h2>
        <el-tag type="success" effect="dark" class="user-count" round>
          共 {{ total }} 位用户
        </el-tag>
      </div>
      <div class="action-section">
        <div class="search-box">
          <el-input
            v-model="searchQuery"
            placeholder="输入用户名搜索，多个用逗号分隔"
            clearable
            @clear="clearSearch"
            @keyup.enter="handleSearch"
            class="search-input"
          />
          <el-button @click="handleSearch" :icon="Search" style="background-color: #555FFF; color: white; border-color: #555FFF">搜索</el-button>
        </div>
        <el-button type="success" :icon="Plus" class="add-user-btn" @click="openAddUserDialog">添加用户</el-button>
      </div>
    </div>
    <div class="main-content">

    <el-card class="table-card" shadow="hover">
      <el-table
        v-loading="loading"
        :data="userList"
        style="width: 100%"
        border
        stripe
        highlight-current-row
        class="user-table"
        empty-text="暂无用户数据"
        height="calc(100vh - 260px)"
        :max-height="'calc(100vh - 300px)'"
        :scrollbar-always-on="true"
      >
        <el-table-column prop="username" label="用户名" min-width="120">
          <template #default="{ row }">
            <div class="user-info">
              <el-avatar :size="32" :src="getUserAvatar(row.username)" class="user-avatar"></el-avatar>
              <span class="username">{{ row.username }}</span>
            </div>
          </template>
        </el-table-column>
        <el-table-column prop="role" label="角色" min-width="120" align="center">
          <template #default="{ row }">
            <el-tag 
              :type="row.username === 'admin' ? 'danger' : 'success'" 
              effect="dark" 
              size="small"
              class="role-tag"
            >
              {{ row.username === 'admin' ? '管理员' : '普通用户' }}
            </el-tag>
          </template>
        </el-table-column>
        <el-table-column prop="created_at" label="注册时间" min-width="180" align="center">
          <template #default="{ row }">
            <el-tooltip :content="formatFullDate(row.created_at)" placement="top">
              <span class="time-info">{{ formatDate(row.created_at) }}</span>
            </el-tooltip>
          </template>
        </el-table-column>
        <el-table-column label="操作" fixed="right" min-width="150" align="center">
          <template #default="{ row }">
            <div class="action-buttons">
              <el-tooltip content="修改密码" placement="top">
                <el-button
                  type="warning"
                  :icon="Key"
                  circle
                  size="small"
                  :disabled="row.role === 'admin'"
                  @click="handleChangePassword(row)"
                />
              </el-tooltip>
              <el-button v-if="row.is_active" type="warning" size="small" @click="ban(row)">禁用</el-button>
              <el-button v-if="!row.is_active" type="success" size="small" @click="activate(row)">激活</el-button>
            </div>
        </template>
      </el-table-column>
    </el-table>

    <div class="table-footer">
      <el-pagination
        v-model:current-page="currentPage"
        v-model:page-size="pageSize"
        background
        :total="total"
        layout="prev, pager, next"
        @current-change="handleCurrentChange"
        class="pagination"
      />
    </div>
    </el-card>
    </div>

    <!-- 用户对话框 -->
    <el-dialog
      v-model="dialogVisible"
      :title="dialogTitle"
      width="450px"
      destroy-on-close
      @closed="resetForm"
      class="user-dialog"
      :close-on-click-modal="false"
      :show-close="false"
    >
      <el-form
        ref="formRef"
        :model="form"
        :rules="rules"
        label-width="80px"
        @keyup.enter="submitForm"
        class="user-form"
        size="large"
      >
        <el-form-item label="用户名" prop="username">
          <el-input 
            v-model="form.username" 
            placeholder="请输入用户名" 
            :prefix-icon="User"
            :disabled="!isAddMode"
          />
        </el-form-item>
        <el-form-item :label="isAddMode ? '密码' : '新密码'" prop="password">
          <el-input 
            v-model="form.password" 
            type="password" 
            :placeholder="isAddMode ? '请输入密码' : '请输入新密码'" 
            show-password 
            :prefix-icon="isAddMode ? Lock : Key"
          />
        </el-form-item>
        
      </el-form>
      <template #footer>
        <div class="dialog-footer">
          <el-button 
            @click="dialogVisible = false"
            class="cancel-btn"
            size="large"
            plain
            style="width: 120px;"
          >取消</el-button>
          <el-button 
            type="primary" 
            @click="submitForm" 
            style="background-color: #555FFF; border-color: #555FFF; width: 120px; margin-bottom: 20px;"
            size="large"
            :loading="submitLoading"
          >
            {{ isAddMode ? '创建' : '确认' }}
          </el-button>
        </div>
      </template>
    </el-dialog>
  </div>
</template>

<script setup lang="ts">
import { ref, onMounted, computed } from 'vue';
import { Search, Plus, Check, Close, User, Lock, Message, Key } from '@element-plus/icons-vue';
import { ElMessage, ElMessageBox } from 'element-plus';
import type { FormInstance, FormRules } from 'element-plus';
import { getUserList, createUser, changePassword, checkUsername, forbidden } from '@/api/auth';
import {useUserStore} from '@/stores/user';

const userStore = useUserStore();
// 搜索相关
const searchQuery = ref('');
const loading = ref(false);

// 分页相关
const currentPage = ref(1);
const pageSize = ref(10);
const total = ref(0);

// 用户列表数据
const userList = ref([]);

// 表单相关
const dialogVisible = ref(false);
const dialogTitle = ref('编辑用户');
const isAddMode = ref(false);
const formRef = ref<FormInstance>();
const submitLoading = ref(false);
const form = ref({
  id: '',
  username: '',
  password: ''
});

// 自定义用户名验证
const validateUsername = async (rule: any, value: string, callback: any) => {
  if(!isAddMode.value){
    // callback()
    return
  }
  // 检查用户名是否为空
  if (!value) {
    callback(new Error('请输入用户名'));
    return;
  }
  
  // 检查用户名是否已存在
  try {
    const response = await checkUsername(value);
    if (response.data.data.exists) {
      callback(new Error('该用户名已存在'));
    } else {
      callback();
    }
  } catch (error) {
    callback(new Error('验证用户名时出错'));
  }
};
// const passwordRegex = /^(?=.*[a-z])(?=.*[A-Z])(?=.*\d)(?=.*[!@#$%^&*()_+\-=$\]{};':"\\|,.<>\/?]).{8,}$/;

// 表单验证规则
const rules = ref<FormRules>({
  username: [
    { required: true, message: '请输入用户名', trigger: 'blur' },
    { min: 2, max: 20, message: '用户名长度应为 2-20 个字符', trigger: 'blur' },
    { pattern: /^[a-zA-Z0-9_-]+$/, message: '用户名只能包含字母、数字、下划线和横杠', trigger: 'blur' },
    { validator: validateUsername, trigger: 'blur' }
  ],
  password: [
    { required: true, message: '请输入密码', trigger: 'blur' },
    { min: 6, max: 20, message: '密码长度应为 6-20 个字符', trigger: 'blur' },
    { 
      pattern: /^(?=.*[a-z])(?=.*[A-Z])(?=.*\d)(?=.*[!@#$%^&*()_+\-=\[\]{};':"\\|,.<>\/?])[A-Za-z\d!@#$%^&*()_+\-=\[\]{};':"\\|,.<>\/?]{6,20}$/,
      // pattern:passwordRegex,
      message: '密码必须包含大小写字母、数字和特殊字符,不能有空格/中文',
      trigger: 'blur' 
    }
  ]
});

// 生成用户头像
const getUserAvatar = (username: string) => {
  // 根据用户名生成固定颜色
  const colors = [
    '#409EFF', '#67C23A', '#E6A23C', '#F56C6C', '#909399',
    '#3963BC', '#41B883', '#B83939', '#6957AF', '#1E9FFF'
  ];
  const index = username.charCodeAt(0) % colors.length;
  
  // 返回空字符串表示使用默认头像
  return '';
};

// 格式化日期（简洁版）
const formatDate = (date: string) => {
  const d = new Date(date);
  return `${d.getFullYear()}-${String(d.getMonth() + 1).padStart(2, '0')}-${String(d.getDate()).padStart(2, '0')}`;
};

// 格式化日期（完整版）
const formatFullDate = (date: string) => {
  return new Date(date).toLocaleString('zh-CN', {
    year: 'numeric',
    month: '2-digit',
    day: '2-digit',
    hour: '2-digit',
    minute: '2-digit',
    second: '2-digit'
  });
};

// 获取用户列表
const fetchUserList = async () => {
  loading.value = true;
  try {
      // 处理搜索查询，支持多个用户名用逗号分隔
      let usernameArray;
      if (searchQuery.value) {
        // 将逗号分隔的字符串分割为数组，并去除空格
        usernameArray = searchQuery.value.split(',').map(name => name.trim()).filter(name => name);
      }
      
      const response = await getUserList({
        skip: currentPage.value,
        limit: pageSize.value,
        username: usernameArray && usernameArray.length > 0 ? usernameArray : undefined
      });
    
    if (response.data.code === 200) {
      userList.value = response.data.data.data;
      total.value = response.data.data.total;
    } else {
      ElMessage.error(response.data.message || '获取用户列表失败');
    }
  } catch (error: any) {
    ElMessage.error(error.message || '获取用户列表失败');
  } finally {
    loading.value = false;
  }
};

// 处理搜索
const handleSearch = () => {
  currentPage.value = 1;
  fetchUserList();
};

// 清空搜索
const clearSearch = () => {
  searchQuery.value = '';
  handleSearch();
};

// 处理页码变化
const handleCurrentChange = (val: number) => {
  currentPage.value = val;
  fetchUserList();
};

// 处理每页条数变化
const handleSizeChange = (val: number) => {
  pageSize.value = val;
  currentPage.value = 1;
  fetchUserList();
};

// 打开添加用户对话框
const openAddUserDialog = () => {
  dialogVisible.value = true;
  dialogTitle.value = '注册';
  isAddMode.value = true;
  resetForm();
};

// 处理修改密码
const handleChangePassword = (row: any) => {
  dialogVisible.value = true;
  dialogTitle.value = '修改密码';
  isAddMode.value = false;
  resetForm();
  form.value = {
    id: row.id,
    username: row.username,
    password: ''
  };
};

// 重置表单
const resetForm = () => {
  if (formRef.value) {
    formRef.value.resetFields();
  }
  form.value = {
    id: '',
    username: '',
    password: ''
  };
};

// 提交表单
const submitForm = async () => {
  if (!formRef.value) return;
  
  submitLoading.value = true;
  
  try {
    // 表单验证
    await formRef.value.validate();
    
    // 根据模式调用不同的 API
    if (isAddMode.value) {
      const response = await createUser({
        username: form.value.username.trim(),
        password: form.value.password
      });
      
      if (response.data.code === 200) {
        ElMessage.success('用户创建成功');
        dialogVisible.value = false;
        fetchUserList();
      } else {
        ElMessage.error(response.data.msg || '创建用户失败');
      }
    } else {
      const response = await changePassword({
        username: form.value.username,
        new_password: form.value.password
      });
      
      if (response.data.code === 200) {
        ElMessage.success('密码修改成功');
        dialogVisible.value = false;

        if(form.value.username === 'admin'){
          // 强制退出，让用户重新登录
          await userStore.logout();
          window.location.reload();
        }
      } else {
        ElMessage.error(response.data.msg || '修改密码失败');
      }
    }
  } catch (error) {
    console.error('创建用户失败:', error);
    if (error.message) {
      ElMessage.error(error.message);
    } else {
      ElMessage.error('用户名/密码错误，请检查输入!');
    }
  } finally {
    submitLoading.value = false;
  }
};
const ban = (row)=>{
 ElMessageBox.confirm('确定禁用么?',
    'Warning',
    {
      confirmButtonText: '确定',
      cancelButtonText: '取消',
      type: 'warning',
    }).then(res=>{
      forbidden({id:row.id,is_active:false}).then(res=>{
        fetchUserList();
      })
    })
 
}
const activate = (row)=>{
  ElMessageBox.confirm('确定激活么?',
    'Warning',
    {
      confirmButtonText: '确定',
      cancelButtonText: '取消',
      type: 'warning',
    }).then(res=>{
      forbidden({id:row.id,is_active:true}).then(res=>{
        fetchUserList();
      })
    })
 
}
// 页面加载时获取用户列表
onMounted(() => {
  fetchUserList();
});
</script>

